* {
  font-family: "Courier New", Courier, monospace;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.anchor {
  scroll-margin-top: 64px;
}

.text-shadow {
  text-shadow: 1px 1px 3px #fffdd0;
  transition: text-shadow 0.05s ease-in;
}
.text-shadow:hover {
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
}

.hidden {
  display: none;
}

nav {
  position: sticky;
  top: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  background-image: linear-gradient(white 85%, transparent);
  z-index: 6;
}
nav .logo {
  height: 60px;
  width: 60px;
  margin-left: 30px;
}
nav .links {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 80%;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
}
main .container {
  padding: 25px 0;
  z-index: 2;
}
main .container .grain {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(grain.svg);
  opacity: 0.25;
  z-index: -2;
}

#visual-slam {
  display: flex;
  justify-content: center;
  width: 100%;
  background-image: linear-gradient(to right, white, white 50%, rgba(135, 206, 235, 0.5));
}
#visual-slam .stuff {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 420px;
  width: 420px;
}
#visual-slam .stuff .desc {
  max-width: 200px;
}
#visual-slam .stuff .desc span {
  text-shadow: 1px 1px 3px #fffdd0;
  vertical-align: middle;
}

#whoami {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: blanchedalmond;
  background-image: linear-gradient(to left, white, blanchedalmond 40%, blanchedalmond);
  width: 100%;
  position: relative;
}
#whoami .slideshow {
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: relative;
  height: 176px;
}
#whoami .slideshow .boyowa {
  position: absolute;
  height: 176px;
  width: auto;
}
#whoami .desc {
  max-width: 400px;
  padding: 0 15px;
}
#whoami .show-more, #whoami .show-less {
  color: palevioletred;
  display: block;
  font-weight: bold;
  padding-top: 10px;
  text-align: center;
  cursor: pointer;
  white-space: nowrap;
}
#whoami .show-more:hover, #whoami .show-less:hover {
  text-decoration: underline;
}
#whoami .show-less {
  font-weight: normal;
  font-size: 15;
}
#whoami .more-to-show {
  display: none;
}
#whoami .text-break {
  display: block;
  font-size: 30px;
  text-align: center;
  padding-top: 10px;
  margin: 0 auto;
}

#connect {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-image: linear-gradient(to bottom right, white, white 50%, rgba(135, 206, 235, 0.5));
}
#connect .connect .title {
  display: block;
  font-size: 21px;
  font-weight: bold;
  padding-bottom: 10px;
  width: 100%;
  text-align: center;
}
#connect .connect form {
  display: flex;
  flex-direction: column;
  width: 300px;
}
#connect .connect form > * {
  margin: 1px 0;
}
#connect .connect form button:hover {
  cursor: pointer;
}
#connect .connect form textarea {
  resize: vertical;
}
#connect .connect .form-input {
  border: 1px solid rgb(135, 206, 235);
  padding: 7px 3px;
  background-image: linear-gradient(to bottom right, white, white 50%, rgba(135, 206, 235, 0.2));
}
#connect .connect .form-input:hover {
  transition: 0.2s ease;
  background-color: rgba(135, 206, 235, 0.2);
  background-image: none;
}
#connect .connect .form-input:focus {
  outline: none;
}
#connect .connect .first-last-name {
  display: grid;
  grid-template-columns: 150px 150px;
}
#connect .connect .first-last-name .first {
  margin-right: 1px;
}
#connect .connect .first-last-name .last {
  margin-left: 1px;
}
#connect .connect .socials {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
}
#connect .connect .socials a {
  display: block;
  margin-top: 5px;
}
#connect .connect .socials a .icon {
  width: 35px;
  height: 35px;
  margin-right: 6px;
}
#connect .connect .socials a .icon:hover {
  transition: 0.2s ease;
  transform: scale(1.1, 1.1);
}
#connect .connect .socials a .icon:active {
  background-color: rgb(135, 206, 235);
  border-radius: 6px;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  color: whitesmoke;
  background-color: rgb(26, 0, 0);
}
.footer #year {
  margin: 0 3px;
}

@media only screen and (max-width: 620px) {
  nav .logo {
    margin-left: 5px;
  }
  #visual-slam .stuff {
    flex-direction: column;
    align-items: center;
  }
  #visual-slam .stuff .desc {
    margin-top: 15px;
  }
}